import React, { forwardRef } from "react";
import styled from "styled-components";

import { ButtonBase } from "../Button/Button.types";
import { colors, fontFamily, Margins } from "../styleguide";

const StyledButtonForBrand = styled.button<Margins>`
	background-color: ${colors.white};
	border: 1px solid transparent;
	box-sizing: border-box;
	align-items: center;
	display: flex;
	font-size: 0.813rem;
	justify-content: center;
	position: relative;
	cursor: pointer;
	border-radius: 24px;
	font-weight: 700;
	line-height: 1.1;
	text-align: center;
	text-decoration: none;
	white-space: nowrap;
	padding: 0;
	margin-bottom: 15px;
	height: 48px;
	width: 48px;
	font-family: ${fontFamily.larsseit};
	font-style: normal;

	&:focus {
		outline: none;
		box-shadow: 0 0 0 2px ${colors.white}, 0 0 0 4px ${colors.midBlue};
	};

	>img {
		position: absolute;
		margin: auto;
		border-radius: 26px;
		width: auto;
	}
`;

export interface ButtonForBrandProps extends Margins, ButtonBase {
	brandIconText?: string
	brandIconImage?: string
}

const ButtonForBrand = forwardRef<HTMLButtonElement, ButtonForBrandProps>((props, ref) => {
	const {
		dataQA,
		brandIconText,
		brandIconImage,
	} = props;
	return (
		<StyledButtonForBrand
			ref={ref}
			data-qa={dataQA}
			{...props}
		>
			{ brandIconImage ? <img src={brandIconImage} alt={brandIconText} data-qa={`${dataQA}-image`} /> : brandIconText}
		</StyledButtonForBrand>
	);
});
ButtonForBrand.displayName = "ButtonForBrand";
export default ButtonForBrand;
